'use client';

import React, { useEffect, useState } from 'react';
import type { Booking, GymClass } from '@/types';

interface BookingWithClass extends Booking {
  gymClass?: GymClass;
}

export default function MyBookingsPage() {
  const [bookings, setBookings] = useState<BookingWithClass[]>([]);
  const [classes, setClasses] = useState<GymClass[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    async function fetchData() {
      try {
        setLoading(true);
        
        // Fetch bookings and classes in parallel
        const [bookingsRes, classesRes] = await Promise.all([
          fetch('/api/bookings'),
          fetch('/api/classes')
        ]);

        if (!bookingsRes.ok) throw new Error('Failed to fetch bookings.');
        if (!classesRes.ok) throw new Error('Failed to fetch classes.');

        const bookingsData = await bookingsRes.json();
        const classesData = await classesRes.json();

        setClasses(classesData);
        
        // Combine bookings with class information
        const bookingsWithClasses = bookingsData.map((booking: Booking) => ({
          ...booking,
          gymClass: classesData.find((cls: GymClass) => cls.id === booking.gymClassId)
        }));

        setBookings(bookingsWithClasses);
      } catch (err: any) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    }
    
    fetchData();
  }, []);

  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('en-US', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric',
    });
  };

  const formatTime = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleTimeString('en-US', {
      hour: '2-digit',
      minute: '2-digit',
    });
  };

  const formatBookingDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('en-US', {
      month: 'short',
      day: 'numeric',
      year: 'numeric',
    }) + ' at ' + date.toLocaleTimeString('en-US', {
      hour: '2-digit',
      minute: '2-digit',
    });
  };

  if (loading) {
    return (
      <div className="flex justify-center items-center min-h-64">
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto mb-4"></div>
          <p className="text-muted-foreground">Loading your bookings...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="text-center py-12">
        <div className="bg-destructive/10 border border-destructive/20 rounded-lg p-6 max-w-md mx-auto">
          <h2 className="text-lg font-semibold text-destructive mb-2">Error Loading Bookings</h2>
          <p className="text-destructive/80">{error}</p>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-8">
      <div className="text-center">
        <h1 className="text-3xl font-bold text-foreground mb-2">My Bookings</h1>
        <p className="text-muted-foreground">
          View and manage your gym class reservations
        </p>
      </div>

      {bookings.length === 0 ? (
        <div className="text-center py-12">
          <div className="bg-muted rounded-lg p-8 max-w-md mx-auto">
            <h2 className="text-lg font-semibold mb-2">No Bookings Found</h2>
            <p className="text-muted-foreground mb-4">
              You haven't booked any classes yet. Browse our available classes to get started!
            </p>
            <a
              href="/"
              className="inline-block px-4 py-2 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 transition-colors"
            >
              Browse Classes
            </a>
          </div>
        </div>
      ) : (
        <div className="space-y-4">
          <div className="text-sm text-muted-foreground mb-4">
            Showing {bookings.length} booking{bookings.length !== 1 ? 's' : ''}
          </div>
          
          {bookings.map((booking) => (
            <div
              key={booking.id}
              className="border border-border rounded-lg p-6 bg-card shadow-sm"
            >
              <div className="flex justify-between items-start">
                <div className="flex-1">
                  <h3 className="text-lg font-semibold mb-2">
                    {booking.gymClass?.title || 'Unknown Class'}
                  </h3>
                  
                  {booking.gymClass && (
                    <div className="space-y-1 text-sm text-muted-foreground mb-4">
                      <p>
                        <span className="font-medium">Instructor:</span> {booking.gymClass.instructor}
                      </p>
                      <p>
                        <span className="font-medium">Date:</span> {formatDate(booking.gymClass.date)}
                      </p>
                      <p>
                        <span className="font-medium">Time:</span> {formatTime(booking.gymClass.date)}
                      </p>
                    </div>
                  )}

                  <div className="space-y-1 text-sm">
                    <p>
                      <span className="font-medium text-foreground">Booked by:</span>{' '}
                      <span className="text-muted-foreground">{booking.userName}</span>
                    </p>
                    <p>
                      <span className="font-medium text-foreground">Email:</span>{' '}
                      <span className="text-muted-foreground">{booking.userEmail}</span>
                    </p>
                    <p>
                      <span className="font-medium text-foreground">Booking Date:</span>{' '}
                      <span className="text-muted-foreground">{formatBookingDate(booking.bookedAt)}</span>
                    </p>
                  </div>
                </div>

                <div className="ml-4">
                  <span className="inline-block px-3 py-1 bg-green-100 text-green-800 text-xs font-medium rounded-full">
                    Confirmed
                  </span>
                </div>
              </div>

              {booking.gymClass?.description && (
                <div className="mt-4 pt-4 border-t border-border">
                  <p className="text-sm text-muted-foreground">
                    {booking.gymClass.description}
                  </p>
                </div>
              )}
            </div>
          ))}
        </div>
      )}

      <div className="text-center pt-8">
        <div className="bg-muted/50 rounded-lg p-6 max-w-2xl mx-auto">
          <h3 className="font-semibold mb-2">Booking Policy</h3>
          <p className="text-sm text-muted-foreground">
            Please arrive 10 minutes before your class starts. Cancellations must be made 
            at least 2 hours in advance. Contact our front desk for any changes to your bookings.
          </p>
        </div>
      </div>
    </div>
  );
}
